import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

const getImageSrc = (props, name) => props.data[name].childImageSharp.responsiveResolution.src;

const Services = (props) => (
    <div>
        <Helmet>
            <title>Webjomp - Services</title>
            <meta name="description" content="Cration de sites Web, d'applications mobiles et conseils pour solutions d'affaires." />
        </Helmet>

        <section id="banner" className="style2">
            <div className="inner">
                <header className="major">
                    <h1>Nos services</h1>
                </header>
                <div className="content">
                    <p>Cration de sites Web, d'applications mobiles et conseils pour solutions d'affaires.</p>
                </div>
            </div>
        </section>

        <div id="main">
            <section id="one">
                <div className="inner">
                    <header className="major">
                        <h2>Plus que des sites Web</h2>
                    </header>
                    <p>En plus de la cration de sites Web, Webjomp offre une gamme varie de services, comme la conception d'applications mobiles pour Android, iOS et Windows Phones, ou encore la cration de logiciels personnaliss aux besoins de votre industrie.</p>
                    <ul className="actions">
                        <li><Link to="#two" className="button scrolly">En savoir plus</Link></li>
                    </ul>
                </div>
            </section>
            <section id="two" className="spotlights services-two">
                <section id="websites">
                    <div className="image" style={{ backgroundImage: `url(${getImageSrc(props, 'websites')})` }}></div>
                    <div className="content">
                        <div className="inner">
                            <header className="major">
                                <h3>Sites Web</h3>
                            </header>
                            <ul className="services-websites--list">
                                <li>Plateforme de commerce lectronique</li>
                                <li>Sites adaptatifs</li>
                                <li>Blogues</li>
                                <li>Rfrencement (SEO)</li>
                                <li>Envoi massif de courriels</li>
                                <li>Rdaction, rvision et traduction</li>
                                <li>Conceptions originales et refontes</li>
                                <li>Pages statiques et dynamiques</li>
                                <li>Intgration de cartes, d'agendas et de rseaux sociaux</li>
                                <li>Plateforme de messagerie et formulaires de contact</li>
                                <li>Analyse de donnes</li>
                                <li>Publicit</li>
                                <li>Acquisition de domaine et hbergement Web</li>
                                <li>Hbergement web</li>
                            </ul>
                            <p>Vos besoins ne se retrouvent pas dans cette liste? N'hsitez pas  <a href="/#contact">communiquer avec nous</a>!</p>
                        </div>
                    </div>
                </section>
                <section id="apps">
                    <div className="image" style={{ backgroundImage: `url(${getImageSrc(props, 'mobileapps')})` }}></div>
                    <div className="content">
                        <div className="inner">
                            <header className="major">
                                <h3>Applications mobiles</h3>
                            </header>
                            <p>Nous dveloppons pour Android, iOS et Windows Phones.</p>
                            <p>Vous avez une ide d'application en tte? Nous vous aidons  la concrtiser  travers ces tapes: cerner les requis, concevoir le look, dvelopper et tester les fonctionnalits, publier l'application.</p>
                            <p>Le travail ne s'arrte pas l! Nous garantissons le bon fonctionnement de votre application en la maintenant  jour et en amliorant la conception et les fonctionnalits grce  l'analyse de donnes de vos usagers.</p>
                        </div>
                    </div>
                </section>
                <section id="consulting">
                    <div className="image" style={{ backgroundImage: `url(${getImageSrc(props, 'consulting')})` }}></div>
                    <div className="content">
                        <div className="inner">
                            <header className="major">
                                <h3>Solutions d'affaires</h3>
                            </header>

                            <p>Concentrez-vous sur les tches dont vous seul tes le professionnel.</p>

                            <h4>Dveloppement de logiciels personnaliss</h4>
                            <p>Visualisation de donnes, automatisation de tches routinires</p>

                            <h4>Installation de logiciels</h4>
                            <p>Facturation, bureautique, comptabilit, salaires, gestion de projet, gestion de clients</p>

                            <h4>Gestion de projet</h4>
                            <p>Prsentations clients, gestion de l'chancier, contrle de qualit et satisfaction, maintien de relations d'affaires</p>

                            <h4>Stratgies d'affaires</h4>
                            <p>Publicit, rfrencement, analyse de donnes, adoption de technologies, conseils pour croissance rapide</p>
                        </div>
                    </div>
                </section>
            </section>
        </div>

    </div>
)

export default Services

export const imageFragment = graphql`
    fragment Image on File {
        childImageSharp {
            responsiveResolution(width: 980, quality: 80) {
                src
            }
        }
    }
`;

export const imageQuery = graphql`
    query ImageQuery {
        websites: file(relativePath: { eq: "services-site-web.png" }) {
            ...Image
        }

        mobileapps: file(relativePath: { eq: "mobile-app.jpg" }) {
            ...Image
        }

        consulting: file(relativePath: { eq: "consulting.jpg" }) {
            ...Image
        }
    }
`;



